import React from 'react';
import { connect } from 'react-redux';
import { showPostInContent } from '../actions'
import PostLink from '../components/PostLink'
import PostLinkList from '../components/PostLinkList'


const mapStateToListProps = (
    state
) => {
    return {
        postLinks: state.postLinks
    };
};
const mapDispatchToListProps = (
    dispatch
) => {
    return {
        onPostLinkClick: (id) => {
            dispatch(showPostInContent(id));
        }
    };
};
const VisiblePostsList = connect(
    mapStateToListProps,
    mapDispatchToListProps
)(PostLinkList);

export default VisiblePostsList


