const postLink = (state, action) => {
    switch (action.type) {
        case 'ADD_TODO':
            return {
                id: action.id,
                text: action.text,
                completed: false
            };
        case 'TOGGLE_TODO':
            if (state.id !== action.id) {
                return state;
            }

            return {
                ...state,
                show: !state.show
            };
        case 'SHOW_THIS_POST_IN_CONTENT':
            if (state.id === action.id) {
                return {
                    ...state,
                    show: true
                };
            }
            else {
                return {
                    ...state,
                    show: false
                };
            }
        default:
            return state;
    }
};

const postLinks = (state = [], action) => {
    switch (action.type) {
        case 'ADD_TODO':
            return [
                ...state,
                postLink(undefined, action)
            ];
        case 'TOGGLE_TODO':
            return state.map(t =>
                postLink(t, action)
            );
        case 'SHOW_THIS_POST_IN_CONTENT':
            return state.map(t =>
                postLink(t, action)
            );
        default:
            return state;
    }
};

export default postLinks


