import React from 'react'
import PropTypes from 'prop-types'

const PostLink = ({
                      onClick,
                      show,
                      title
                  }) => (
    <li
        onClick={onClick}
        style={{
            textDecoration:
                show ?
                    'font-weight' : 'bold'
        }}
    >
        {title}
    </li>
)

// PostLink.propTypes = {
//     onClick: PropTypes.func.isRequired,
//     selected: PropTypes.bool.isRequired,
//     text: PropTypes.string.isRequired
// }

export default PostLink

