import React from 'react';
import { Link } from 'react-router-dom'
class Header extends React.Component {

    render() {
        return (
            <div className="App-header">
                <h2>Title</h2>
                <Link to="/">Blog</Link>
                <Link to="/about">About</Link>
            </div>
        )
    }
}

export default Header;



